<template>
    <el-card class="classification-management">
        <!-- 分类选择/添加 -->
        <el-row class="mb30 ml20">
            <el-col :span="8">
                <span class="mr5">一级分类：</span>
                <el-select
                    v-model="classify.value"
                    placeholder="请选择分类"
                    clearable
                    @change="classifyChange"
                    @clear="getClassify"
                >
                    <!-- TODO: 此处需要循环显示 -->
                    <el-option label="手机" value="手机" :style="{'min-width': minWidth + 'px'}"></el-option>
                    <el-option label="配件" value="配件"></el-option>
                </el-select>
            </el-col>
            <el-col :span="2">
                <el-button
                    type="primary"
                    @click="title = '新增分类', dialogFormVisible = true"
                    icon="el-icon-circle-plus-outline"
                >新增分类</el-button>
            </el-col>
        </el-row>

        <!-- 表格 -->
        <el-table
            v-loading="!tableData.table.length ? true : false"
            :data="tableData.table"
            border
            stripe
        >
            <el-table-column type="index"></el-table-column>
            <el-table-column align="center" prop="level1" label="一级分类名称"></el-table-column>
            <el-table-column align="center" prop="level2" label="二级分类名称"></el-table-column>
            <el-table-column align="center" prop="number" label="二级分类 ID"></el-table-column>
            <el-table-column align="center" label="操作">
                <template slot-scope="scope">
                    <el-tooltip content="修改" placement="top" :enterable="false">
                        <el-button
                            type="primary"
                            size="mini"
                            icon="el-icon-edit"
                            @click="modify(scope.row)"
                        ></el-button>
                    </el-tooltip>
                    <el-tooltip content="查看详情" placement="top" :enterable="false">
                        <el-button
                            @click="handleClick(scope.row)"
                            type="warning"
                            size="mini"
                            icon="el-icon-view"
                        ></el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>

        <!-- 分页 -->
        <div class="paging mt10">
            <el-pagination
                background
                layout="prev, pager, next, jumper"
                :total="tableData.total"
                @current-change="currentChange"
            ></el-pagination>
        </div>

        <!-- 新增分类 -->
        <el-dialog
            :title="title"
            :visible.sync="dialogFormVisible"
            :show-close="disabled"
            :close-on-click-modal="disabled"
            :close-on-press-escape="disabled"
            @closed="disabled = false"
        >
            <el-form
                :model="newClassification"
                :rules="rules"
                ref="newClassification"
                :disabled="disabled"
                @submit.native.prevent="onSubmit"
                label-width="100px"
            >
                <el-form-item label="一级分类" prop="level1">
                    <el-select v-model="newClassification.level1" placeholder="请选择一级分类" clearable>
                        <!-- TODO: 此处需要循环 -->
                        <el-option label="手机" value="手机"></el-option>
                        <el-option label="配件" value="配件"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="二级分类" prop="level2">
                    <el-input v-model="newClassification.level2" placeholder="添加二级分类" clearable></el-input>
                </el-form-item>

                <el-form-item label="参数" class="mt20" prop="parameter">
                    <el-table :data="newClassification.parameter" border stripe>
                        <el-table-column width="60px" align="center">
                            <template slot="header" slot-scope="scope">
                                <el-tooltip content="添加" placement="top" v-show="!disabled">
                                    <!-- TAG: 添加参数 { item: '' , value: ''} -->
                                    <i
                                        class="del el-icon-circle-plus-outline f22"
                                        @click="newClassification.parameter.push({item: '', value: ''})"
                                    ></i>
                                </el-tooltip>
                                {{ !disabled ? '' : '#' }}
                            </template>
                            <template slot-scope="scope">{{scope.$index + 1}}</template>
                        </el-table-column>

                        <el-table-column label="参数项" align="center">
                            <template slot-scope="scope">
                                <el-input v-model="scope.row.item" size="mini" />
                            </template>
                        </el-table-column>

                        <el-table-column width="60px" label="操作" align="center" v-if="!disabled">
                            <template slot-scope="scope">
                                <el-tooltip content="移除" placement="top">
                                    <i
                                        class="del el-icon-delete f22"
                                        @click="newClassification.parameter.splice(scope.$index, 1)"
                                    ></i>
                                </el-tooltip>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>

                <el-form-item style="text-align: right; margin: 0" v-show="!disabled">
                    <el-button @click="cancel">取 消</el-button>
                    <el-button type="primary" native-type="submit">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </el-card>
</template>

<script>
import { mixin } from "../../mixins/resize";

export default {
    mixins: [mixin],
    data() {
        /* 参数项校验 */
        let checkTable = (rule, value, callback) => {
            if (!value.length) {
                return callback(new Error("参数至少要有一项"));
            } else if (value.some(({ item }) => item === "")) {
                callback(new Error("参数不能为空"));
            } else {
                callback();
            }
        };
        return {
            /* 分类 */
            classify: {
                value: "",
                classifyData: []
            },
            /* 新增分类 */
            dialogFormVisible: false,
            /* 是否禁用 */
            disabled: false,
            title: '新增分类',
            newClassification: {
                level1: "",
                level2: "",
                parameter: [{ item: "cpu", value: "" }]
            },
            rules: {
                level1: [
                    {
                        required: true,
                        message: "请选择一级分类",
                        trigger: "change"
                    }
                ],
                level2: [
                    {
                        required: true,
                        message: "请输入二级分类",
                        trigger: "blur"
                    }
                ],
                parameter: [{ validator: checkTable, required: true, trigger: "blur" }]
            },
            /* 表格数据 */
            tableData: {
                table: [],
                total: null
            }
        };
    },
    methods: {
        /**
         *  TODO: 分类查询
         */
        classifyChange(value) {
            console.log(value);
        },
        /**
         *  TODO: 表格数据获取并渲染
         */
        async getClassify() {
            const { res, total } = await this.$get("/getClassify");
            this.tableData.table = res;
            this.tableData.total = total;
        },
        /**
         *  TODO: 分页
         */
        async currentChange(value) {
            const { res } = await this.$get(`/getClassify?page=${value}`);
            this.tableData.table = res;
        },
        /**
         *  TODO: 提交
         */
        onSubmit() {
            console.log(this.newClassification);
            this.$refs.newClassification.validate(valid => {
                if (valid) {
                    this.dialogFormVisible = false;
                    this.$refs.newClassification.resetFields();
                    alert("此处提交数据!");
                } else {
                    return false;
                }
            });
        },
        /**
         *  取消按钮
         */
        cancel() {
            this.$confirm("确定取消?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                this.dialogFormVisible = false;
                this.$refs.newClassification.resetFields();
                this.$message({
                    type: "success",
                    message: "已取消"
                });
            });
        },
        /**
         *  TODO: 查看
         */
        handleClick(value) {
            console.log(value);
            
            this.title = '详情'
            this.dialogFormVisible = true;
            this.disabled = true;
        },
        /**
         *  TODO: 修改
         */
        modify(value) {
            console.log(value);

            this.title = '修改分类'
            this.dialogFormVisible = true
        }
    },
    created() {
        this.getClassify();
    },
    mounted() {
        console.log(this.minWidth);
    }
};
</script>

<style scoped>
.classification-management {
    width: 100%;
    color: #606266;
    background-color: #fff;
}

.paging {
    text-align: right;
}

.parameter-input {
    width: 80px;
}

.del {
    cursor: pointer;
    transition: all 0.3s;
}

.del:hover {
    color: #409eff;
    transform: scale(1.1);
}
</style>



